import { Form, Input, Button, Popover } from 'antd';
import React from 'react';
import req from '@/utils/req';
import CodeImg from '@/assets/code.png';
import { history } from 'umi';
import styles from './index.less';

interface FormValues {
  n: string;
  co: string;
}

const layout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 16 },
};

const tailLayout = {
  wrapperCol: { offset: 6, span: 16 },
};

const content = (
  <>
    <div style={{ textAlign: 'center' }}>
      <img style={{ width: '180px' }} src={CodeImg} alt="-" />
    </div>
    <p style={{ width: '240px' }}>"" </p>
  </>
);

const Login = () => {
  const onFinish = (values: FormValues) => {
    const { n, co } = values;
    req.post('/vip/check', { n, co }).then((res: any) => {
      localStorage.setItem('nickname', res.n);
      localStorage.setItem('h5', JSON.stringify(res.h5));
      history.push('/');
    });
  };

  return (
    <div className={styles.loginWrap}>
      <Form
        {...layout}
        name="login"
        className={styles.formWrap}
        initialValues={{ remember: true }}
        onFinish={onFinish}
      >
        <div className={styles.tit}>
          Dooring
          <span style={{ marginLeft: '20px', fontSize: '18px', color: '#06c' }}></span>
        </div>
        <Form.Item label="" name="n" rules={[{ required: true, message: '!' }]}>
          <Input />
        </Form.Item>

        <Form.Item label="" name="co" rules={[{ required: true, message: '!' }]}>
          <Input.Password />
        </Form.Item>

        <div style={{ margin: '-16px 0 12px 116px' }}>
          <Popover placement="topLeft" content={content} trigger="click">
            <Button type="link"></Button>
          </Popover>
        </div>

        <Form.Item {...tailLayout}>
          <Button type="primary" htmlType="submit" block>
            
          </Button>
        </Form.Item>
        <div className={styles.safeWrap}>
          
          <Button type="link">Dooring</Button>
        </div>
      </Form>
    </div>
  );
};

export default Login;
